"use client";

import { useEffect, useState } from "react";
import { useAudio } from "@/context/AudioContext";
import { Plus, Play, Trash2, Music } from "lucide-react";

type Track = {
  id: number;
  title: string;
  artist: string;
  album?: string;
  url: string;
  cover?: string;
};

type PlaylistTrack = {
  id: number;
  track: Track;
  position: number;
};

type Playlist = {
  id: number;
  name: string;
  description?: string;
  tracks: PlaylistTrack[];
};

export default function PlaylistsPage() {
  const [playlists, setPlaylists] = useState<Playlist[]>([]);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newPlaylistName, setNewPlaylistName] = useState("");
  const [newPlaylistDescription, setNewPlaylistDescription] = useState("");
  const { playPlaylist } = useAudio();

  useEffect(() => {
    fetchPlaylists();
  }, []);

  const fetchPlaylists = async () => {
    try {
      const res = await fetch("/api/playlists");
      const data = await res.json();
      setPlaylists(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error("Erreur chargement playlists:", error);
      setPlaylists([]);
    }
  };

  const createPlaylist = async () => {
    if (!newPlaylistName.trim()) return;

    try {
      const res = await fetch("/api/playlists", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: newPlaylistName,
          description: newPlaylistDescription,
        }),
      });

      if (res.ok) {
        setNewPlaylistName("");
        setNewPlaylistDescription("");
        setShowCreateModal(false);
        fetchPlaylists();
      }
    } catch (error) {
      console.error("Erreur création playlist:", error);
    }
  };

  const deletePlaylist = async (id: number) => {
    if (!confirm("Supprimer cette playlist ?")) return;

    try {
      await fetch(`/api/playlists/${id}`, { method: "DELETE" });
      fetchPlaylists();
    } catch (error) {
      console.error("Erreur suppression:", error);
    }
  };

  const playEntirePlaylist = (playlist: Playlist) => {
    const tracks = playlist.tracks
      .sort((a, b) => a.position - b.position)
      .map((pt) => pt.track);
    playPlaylist(tracks);
  };

  return (
    <div className="max-w-7xl mx-auto">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold">Mes Playlists</h1>
        <button
          onClick={() => setShowCreateModal(true)}
          className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg"
        >
          <Plus size={20} />
          Nouvelle Playlist
        </button>
      </div>

      {playlists.length === 0 ? (
        <div className="text-center py-12 text-gray-400">
          <Music size={64} className="mx-auto mb-4 opacity-50" />
          <p>Aucune playlist pour le moment</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {playlists.map((playlist) => (
            <div
              key={playlist.id}
              className="bg-gray-100 dark:bg-gray-800 rounded-lg p-4 hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors"
            >
              <div className="flex justify-between items-start mb-3">
                <div className="flex-1">
                  <h3 className="text-lg font-semibold">{playlist.name}</h3>
                  {playlist.description && (
                    <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                      {playlist.description}
                    </p>
                  )}
                  <p className="text-xs text-gray-400 mt-2">
                    {playlist.tracks.length} titre{playlist.tracks.length > 1 ? "s" : ""}
                  </p>
                </div>
                <button
                  onClick={() => deletePlaylist(playlist.id)}
                  className="p-2 hover:bg-red-500 hover:text-white rounded-lg transition-colors"
                  title="Supprimer"
                >
                  <Trash2 size={18} />
                </button>
              </div>

              <button
                onClick={() => playEntirePlaylist(playlist)}
                disabled={playlist.tracks.length === 0}
                className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 disabled:cursor-not-allowed rounded-lg"
              >
                <Play size={18} />
                Lire la playlist
              </button>
            </div>
          ))}
        </div>
      )}

      {/* Modal de création */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white dark:bg-gray-800 rounded-lg p-6 max-w-md w-full mx-4">
            <h2 className="text-xl font-bold mb-4">Nouvelle Playlist</h2>
            <input
              type="text"
              placeholder="Nom de la playlist"
              value={newPlaylistName}
              onChange={(e) => setNewPlaylistName(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg mb-3 bg-white dark:bg-gray-700"
              autoFocus
            />
            <textarea
              placeholder="Description (optionnelle)"
              value={newPlaylistDescription}
              onChange={(e) => setNewPlaylistDescription(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg mb-4 bg-white dark:bg-gray-700 resize-none"
              rows={3}
            />
            <div className="flex gap-3">
              <button
                onClick={() => setShowCreateModal(false)}
                className="flex-1 px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700"
              >
                Annuler
              </button>
              <button
                onClick={createPlaylist}
                className="flex-1 px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg text-white"
              >
                Créer
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
